import { CHANGE_SAMPLE } from '../mutation-types';

//       .
export const Page = {
  state: () => ({name: '', tag:''}),
  mutations: {
    [CHANGE_SAMPLE.CHANGE_SAMPLE](state, value) {
      state.name = value;
    },
    [CHANGE_SAMPLE.CHANGE_TEMP] () {

    }
  },
  getters: {
    // state   name   .
    pageInfo(state) {
      return state.name;
    }
  },
  actions: {
    changeName({ commit }, value) {
      commit(CHANGE_SAMPLE.CHANGE_SAMPLE, value);
    },
  }
};